@import "../../styles/sass/variables";

.button {
  border: 1px solid;
  width: 120px;
  border-radius: 5px;
  opacity: 0.8;
  background-color: $bgColor;
  font-family: Inter, sans-serif;
  font-size: 16px;
  line-height: 1.7;
  color: $textColor;
  position: relative;
  outline: none;
  z-index: 0;

  &::before {
    content: "";
    position: absolute;
    width: 0px;
    height: 0px;
    background: $primaryColor;
    bottom: 0;
    left: 50%;
    top: 50%;
    z-index: -1;
    transition: all 0.3s ease-in-out;
  }

  &:hover {
    text-decoration: none;
    color: $textColor;

    &::before {
      width: 100%;
      height: 100%;
      transition: all 0.5s ease-in-out 0s;
      left: 0;
      top: 0;
      border-radius: 5px;
    }
  }
}